import { useState } from "react";
import { useNavigate } from "react-router-dom";
import api from "../../../api/http";
import PageHeader from "../../../components/PageHeader";

export default function PneuCreate() {
  const navigate = useNavigate();
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState("");
  
  const [form, setForm] = useState({
    codigo: "",
    numero_fogo: "",
    medida: "",
    marca: "",
    modelo: "",
    tipo: "NOVO",
    dot: "",
    ano_fabricacao: "",
    semana_fabricacao: "",
    status: "ESTOQUE",
    posicao_atual: "",
    km_total: "0",
    km_atual: "0",
    profundidade_sulco: "",
    numero_recapagens: "0",
    pode_recapar: true,
    valor_compra: "",
    valor_atual: "",
    data_aquisicao: "",
    observacoes: ""
  });

  const onChange = (e) => {
    const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
    setForm({ ...form, [e.target.name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);
    setError("");

    try {
      const payload = {
        codigo: form.codigo,
        numero_fogo: form.numero_fogo || null,
        medida: form.medida,
        marca: form.marca || null,
        modelo: form.modelo || null,
        tipo: form.tipo,
        dot: form.dot || "",
        ano_fabricacao: form.ano_fabricacao ? parseInt(form.ano_fabricacao) : null,
        semana_fabricacao: form.semana_fabricacao ? parseInt(form.semana_fabricacao) : null,
        status: form.status,
        posicao_atual: form.posicao_atual || "",
        km_total: parseInt(form.km_total) || 0,
        km_atual: parseInt(form.km_atual) || 0,
        profundidade_sulco: form.profundidade_sulco ? parseFloat(form.profundidade_sulco) : null,
        numero_recapagens: parseInt(form.numero_recapagens) || 0,
        pode_recapar: form.pode_recapar,
        valor_compra: form.valor_compra ? parseFloat(form.valor_compra) : 0,
        valor_atual: form.valor_atual ? parseFloat(form.valor_atual) : 0,
        data_aquisicao: form.data_aquisicao || null,
        observacoes: form.observacoes || null
      };

      await api.post("/api/transportador/pneus/pneus/", payload);
      navigate("/dashboard/pneus/lista");
    } catch (ex) {
      console.error("Erro ao criar pneu:", ex);
      setError(ex.response?.data?.detail || "Falha ao criar pneu. Verifique os dados e tente novamente.");
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <PageHeader 
        title="Novo Pneu" 
        subtitle="Cadastrar novo pneu no estoque"
      />

      <div className="bg-white rounded-xl shadow-md p-6 max-w-4xl">
        {error && (
          <div className="mb-4 p-4 bg-red-50 border border-red-200 text-red-700 rounded-lg">
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Identificação */}
          <div>
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Identificação</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Código <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  name="codigo"
                  value={form.codigo}
                  onChange={onChange}
                  required
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="XBRI-29580225"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Número de Fogo
                </label>
                <input
                  type="text"
                  name="numero_fogo"
                  value={form.numero_fogo}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="NF12345"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Medida <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  name="medida"
                  value={form.medida}
                  onChange={onChange}
                  required
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="295/80R22.5"
                />
              </div>
            </div>
          </div>

          {/* Especificações */}
          <div>
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Especificações</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Marca</label>
                <input
                  type="text"
                  name="marca"
                  value={form.marca}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Bridgestone, Michelin..."
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Modelo</label>
                <input
                  type="text"
                  name="modelo"
                  value={form.modelo}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="R297, XZA2..."
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Tipo</label>
                <select
                  name="tipo"
                  value={form.tipo}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="NOVO">Novo</option>
                  <option value="RECAPADO">Recapado</option>
                  <option value="REFORMADO">Reformado</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">DOT</label>
                <input
                  type="text"
                  name="dot"
                  value={form.dot}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="2424"
                  maxLength="4"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Ano Fabricação</label>
                <input
                  type="number"
                  name="ano_fabricacao"
                  value={form.ano_fabricacao}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="2024"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Semana Fabricação</label>
                <input
                  type="number"
                  name="semana_fabricacao"
                  value={form.semana_fabricacao}
                  onChange={onChange}
                  min="1"
                  max="52"
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="24"
                />
              </div>
            </div>
          </div>

          {/* Status e Localização */}
          <div>
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Status e Localização</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Status</label>
                <select
                  name="status"
                  value={form.status}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="ESTOQUE">Em Estoque</option>
                  <option value="MONTADO">Montado</option>
                  <option value="MANUTENCAO">Em Manutenção</option>
                  <option value="SUCATA">Sucata</option>
                  <option value="RECAPAGEM">Em Recapagem</option>
                  <option value="VENDIDO">Vendido</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Posição Atual
                </label>
                <input
                  type="text"
                  name="posicao_atual"
                  value={form.posicao_atual}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="ABC1234-1E"
                />
              </div>
            </div>
          </div>

          {/* Dados de Uso */}
          <div>
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Dados de Uso</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  KM Total Rodado
                </label>
                <input
                  type="number"
                  name="km_total"
                  value={form.km_total}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="0"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  KM Atual (desde montagem)
                </label>
                <input
                  type="number"
                  name="km_atual"
                  value={form.km_atual}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="0"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Profundidade Sulco (mm)
                </label>
                <input
                  type="number"
                  step="0.01"
                  name="profundidade_sulco"
                  value={form.profundidade_sulco}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="16.00"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Número de Recapagens
                </label>
                <input
                  type="number"
                  name="numero_recapagens"
                  value={form.numero_recapagens}
                  onChange={onChange}
                  min="0"
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="0"
                />
              </div>

              <div className="flex items-center pt-6">
                <input
                  type="checkbox"
                  name="pode_recapar"
                  checked={form.pode_recapar}
                  onChange={onChange}
                  className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                />
                <label className="ml-2 text-sm font-medium text-gray-700">
                  Pode Recapar
                </label>
              </div>
            </div>
          </div>

          {/* Valores */}
          <div>
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Valores</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Valor de Compra (R$)
                </label>
                <input
                  type="number"
                  step="0.01"
                  name="valor_compra"
                  value={form.valor_compra}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="0.00"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Valor Atual (R$)
                </label>
                <input
                  type="number"
                  step="0.01"
                  name="valor_atual"
                  value={form.valor_atual}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="0.00"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Data de Aquisição
                </label>
                <input
                  type="date"
                  name="data_aquisicao"
                  value={form.data_aquisicao}
                  onChange={onChange}
                  className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
            </div>
          </div>

          {/* Observações */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Observações</label>
            <textarea
              name="observacoes"
              value={form.observacoes}
              onChange={onChange}
              rows="3"
              className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Informações adicionais sobre o pneu..."
            />
          </div>

          {/* Botões */}
          <div className="flex items-center gap-3 pt-4 border-t">
            <button
              type="submit"
              disabled={saving}
              className="px-6 py-2 rounded-lg bg-gradient-to-r from-blue-400 via-indigo-500 to-purple-600 text-white font-medium hover:opacity-90 transition-opacity disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {saving ? "Salvando..." : "Salvar Pneu"}
            </button>
            
            <button
              type="button"
              onClick={() => navigate("/dashboard/pneus/lista")}
              className="px-6 py-2 rounded-lg border border-gray-300 text-gray-700 font-medium hover:bg-gray-50 transition-colors"
            >
              Cancelar
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

